.btn{
    background-color: #222;
    color: #fff;
    padding: .7em 1.2em;
    text-decoration: none;
    transition: .5s;
    cursor: pointer;
    border: none;
}

.btn:hover{
    color:#ffbb33
}

.form_control {
    display: flex;
    flex-direction: column;
    margin-bottom: 1em;
}
.form_control label{
    margin-bottom: .6em;
    font-weight: bold;
}

.form_control button{
    padding: .7em;
    border-radius: 0;
    border: none
}

.form_control Select::placeholder{
    color: #7b7b7b;
}